.App{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.3)
  ),
  url(../img/bg.jpg);
  background-size:cover;
  background-position: center;
}

form{
  background-color: white;
  padding: 0 60px;
  border-radius: 10px;
}

h1{
  color: rgb(0, 0, 83);
  text-align: center;
}

button{
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: rgb(0, 0, 83);
  color:white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
}